@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,500;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

html, body, #root {
  height: 100%;
  font-family: 'Roboto', sans-serif;
}

.header-div {
  background-color: #343a40!important;
  position: fixed;
  z-index: 1;
}

.greeting{
  font-weight: 100;
}

.skill-icon-div{
  top: -40px;
  left: 40%;
}

.profile-img{
  width: 200px;
  height: 200px;
}

.contact-profile-img{
  height: 100px;
  width: 100px;
}

.card-img-div{
  height: 200px;
}

.card-img-div img{
  height: inherit;
}

.car-overlay-opacity{
  opacity: 0.9;
}

.car-overlay{
  display: none;
  width: 100%;
  background-color: grey;
  color: aliceblue;
  height: 200px;
  top: 0;
}

.card-div:hover .car-overlay {
  display: block;
}

.summary{
  background-color: transparent;
}

.bg-portfolio{
  background-color: #3f6466;
}


/* ### TOAST ### */
.custom-toast-container {
  background-color: white;
  border-radius: 4px;
  color: black;
}

.custom-toast-info-container {
  border: 1px solid #76a2e4;
  border-left: 10px solid #9fbae2;
}

.custom-toast-success-container {
  border: 1px solid #5ca85c;
  border-left: 10px solid #5ca85c;
}

.custom-toast-error-container {
  border: 1px solid red;
  border-left: 10px solid red;
}

/* ### scroll bar ### */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  /* border-radius: 10px; */
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: grey;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: black;
}

